html {
  font: 14px/18px @arial;
  color: black;
}

.h1 {
  font: 36px/36px @oswald;
}

.h2 {
  font: 36px/40px @oswald;
}

@media (min-width: @tablet) {
  html {
    font: 16px/24px @arial;
    color: black;
  }

  .h1 {
    font: 60px/60px @oswald;
  }

  .h2 {
    font: 36px/36px @oswald;
  }
}

@media (min-width: @desktop) {
  html {
    font: 16px/24px @arial;
    color: black;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
